.YourAccount {
  .Stack {
    --gap: var(--s-2);
  }

  h3,
  div[role="separator"] {
    margin: 0;
  }

  span {
    display: block;
    font-size: var(--s-1);
  }

  span:last-child {
    font-family: Montserrat-Bold;
  }

  span,
  button {
    overflow-wrap: anywhere;
  }

  button {
    height: 100%;
    white-space: normal;
  }
}

